  opacity: 0;
  transition-property: opacity, transform;
  transition-duration: 900ms;
  transition-timing-function: cubic-bezier(0.16, 1, 0.3, 1);
  will-change: opacity, transform;
}

.scroll-fade-up {
  transform: translateY(50px);
}

.scroll-fade-down {
  transform: translateY(-50px);
}

.scroll-fade-left {
  transform: translateX(-70px);
}

.scroll-fade-right {
  transform: translateX(70px);
}

.scroll-scale-up {
  transform: scale(0.9);
}

.scroll-fade-in {
  transform: none;
}

